@import "~@/assets/css/_variables.scss";

main {
  background-blend-mode: multiply;
  background-color: rgba($color: #000, $alpha: 0.4);
  background-image: url(../assets/image/中文3.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}
.page-block {
  border-radius: 8px;
  padding: 32px 24px;
  background: #fff;
  box-shadow: 0 4px 16px rgba($color: #000, $alpha: 0.16);
  .page-title {
    position: relative;
    display: flex;
    justify-content: center;
    margin: 16px 0 48px;
    h2 {
      background: #fff;
      padding: 0 32px;
      z-index: 1;
    }
    &::after {
      position: absolute;
      content: "";
      width: 100%;
      height: 2px;
      left: 0;
      top: 50%;
      background: $c-main;
    }
  }
  .rule-block {
    h3 {
      border-radius: 8px;
      margin: 32px 0;
      padding: 4px 0;
      background: #eee;
      font-size: $fs * 1.2;
      text-align: center;
    }
    > ol {
      counter-reset: olListCounter;
      font-weight: bold;
      > li {
        position: relative;
        font-size: $fs * 1.2;
        &::before {
          display: inline-block;
          border-radius: 4px 4px 0 0;
          padding: 2px 12px;
          color: #fff;
          background: $c-main;
          counter-increment: olListCounter;
          content: counter(olListCounter);
          margin-right: 0.5em;
        }
        &::after {
          position: absolute;
          content: "";
          margin: 0 calc(var(--bs-gutter-x) * 0.5);
          width: calc(100% - calc(var(--bs-gutter-x) * 0.5));
          height: 1px;
          left: 0;
          top: $fs * 2;
          background: $c-main;
        }
        > h4 {
          display: inline-block;
          margin-left: 4px;
          letter-spacing: 1px;
        }
        > ol {
          margin-top: 8px;
          list-style: inside normal;
          font-weight: normal;
          font-size: $fs;
          > li {
            margin: 4px 0;
            transition: background-color 0.2s;
            &:hover {
              color: #000;
              background-color: #f5f5f5;
            }
          }
        }
      }
    }
    .list-style-none {
      font-weight: normal;
      li {
        margin: 4px 0;
        font-size: $fs;
        transition: background-color 0.2s;
        &:hover {
          color: #000;
          background-color: #eee;
        }
        &::before {
          margin: 0;
          margin-right: 4px;
          padding: 0;
          color: #333;
          background: none;
          content: counter(olListCounter) ".";
        }
      }
    }
  }
}
